import React from 'react'
import Container from '../../components/Container'
import * as s from './styles'
import student from '../../assets/student.svg'
import {
  Chart as ChartJS,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
} from 'chart.js'
import { Radar } from 'react-chartjs-2'

ChartJS.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend
)

const labels = [
  ['  :', ' -'],
  ['  .. '],
  ['  '],
  ['- ', '(, )'],
  ['  ', ',  '],
  ['  '],
  [' Vark'],
]

export const Cabinet = () => {
  const data = {
    labels,
    datasets: [
      {
        label: '%  ',
        data: [0, 0, 71, 0, 0, 0, 0],
        backgroundColor: 'transparent',
        borderColor: 'rgba(255, 99, 132, 1)',
        borderWidth: 6,
      }
    ]
  }

  const options = {
    responsive: true,
    tooltip: {
      enabled: true,
    },
    layout: {
      beginAtZero: true,
      padding: {
        right: 35,
        bottom: 60
      }
    },
    scales: {
      r: {
        min: 0,
        max: 100,
        gridLines: {
          circular: false
        },
        ticks: {
          callback: (value) => {
            return `${value}%`;
          },
        },
        pointLabels: {
          maxWidth: 100,
          padding: 10,
          font: {
            size: 14
          },
          callback(value) {
            return value
          }
        }
      }
    },
  }

  return (
    <s.Section>
      <Container>
        <s.H1>
           
        </s.H1>
        <s.Profile>
          <s.ProfilePhoto>
            <img alt="avatar" src={student} />
          </s.ProfilePhoto>
          <s.ProfileData>
            <s.ProfileTitle>
               
            </s.ProfileTitle>
            <s.Data>
              <s.Info>
                <b> :</b>   
              </s.Info>
              <s.Info>
                <b>E-mail:</b> user@mail.ru
              </s.Info>
              <s.Info>
                <b>:</b> userLogin
              </s.Info>
              <s.Info>
                <b> :</b> 01.01.2001
              </s.Info>
            </s.Data>
          </s.ProfileData>
        </s.Profile>
        <s.Table>
          <tbody>
            <s.Tr>
              <s.Td>  :  -</s.Td>
              <s.Td>
                  - 3
                <br />
                  - 2
                <br />
                 - 4
                <br />
                  - 3
                <br />
                 - 2
                <br />
                 - 5
                <br />
                 - 1
                <br />
                  - 3
              </s.Td>
            </s.Tr>
            <s.Tr>
              <s.Td>  .. </s.Td>
              <s.Td>-</s.Td>
            </s.Tr>
            <s.Tr>
              <s.Td>  </s.Td>
              <s.Td>-</s.Td>
            </s.Tr>
            <s.Tr>
              <s.Td>-  (, )</s.Td>
              <s.Td>-</s.Td>
            </s.Tr>
            <s.Tr>
              <s.Td>   ,  </s.Td>
              <s.Td>-</s.Td>
            </s.Tr>
            <s.Tr>
              <s.Td>  </s.Td>
              <s.Td>-</s.Td>
            </s.Tr>
            <s.Tr>
              <s.Td> Vark</s.Td>
              <s.Td>-</s.Td>
            </s.Tr>
          </tbody>
        </s.Table>
       {/* <s.ChartWrapper>
          <Radar type='radar' data={data} options={options} />
        </s.ChartWrapper> */}
      </Container>
    </s.Section>
  )
}
